@import "../../global.scss";

.badgesCertifications {
  background-color: #bc7b82;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: auto;
  gap: 30px;
  width: 100%;

  h1 {
    font-size: 25px;
    color: #ffebcd;
    margin: 15px auto;
    overflow: hidden;

    @include mobile {
      font-size: 20px;
      margin-top: 5px;
    }
  }

  .hrstyle {
    border: 0.5px solid #ffebcd;
    background-color: #ffebcd;
    width: 90vw;
    margin: 0 auto;
  }

  .container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    // gap: 20px;
    flex-wrap: wrap;
    padding-bottom: 20px;

    .item {
      width: 200px;
      height: 150px;
      border-radius: 10px;
      background-color: #bc7b82;
      margin: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.5s ease;
      // box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
      box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
      cursor: pointer;
      overflow: hidden;
      transition: all 1s ease;
    //   width: 210px;
    // height: 199px;
    // border-radius: 10px;
    // background-color: #ffffff5c;
    // margin: 20px;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    // transition: all 0.5s ease;
    // /* box-shadow: rgb(0 0 0 / 6%) 0px 2px 4px 0px inset; */
    // cursor: pointer;
    // overflow: hidden;
    // transition: all 1s ease;

      &:hover {
        transform: scale(1.1);
      }

      @include mobile {
        margin: 0;
        width: 150px;
        box-shadow: none;
      }

      img {
        width: 90%;
        height: 90%;
        margin-left: 10px;
        margin-right: 10px;
        object-fit: contain;
        flex: 0.6;

        // width: 90%;
        // height: 90%;
        // background-size: cover;
        // border-radius: 20px;
        // object-fit: contain;
    

        @include mobile {
          overflow: hidden;
        }
      }
    }
  }
}
